import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Home from "./routes/Home.js";
import Details from "./routes/Details.js";
import Papa from "papaparse";
import { useEffect, useState } from "react";

function App() {
  const myURL = "moving-sale";

  // item list
  const [itemList, setItemList] = useState([]);
  useEffect(() => {
    const getItemList = () => {
      fetch(require("./items.csv"))
        .then((response) => response.text())
        .then((responseText) => {
          const parsedText = Papa.parse(responseText);
          setItemList(parsedText.data);
        });
    };
    getItemList();
  }, []);

  return (
    <Router>
      <Routes>
        <Route path={`${myURL}`} element={<Home itemList={itemList} />} />
        <Route
          path={`${myURL}/item/:id`}
          element={<Details itemList={itemList} />}
        />
      </Routes>
    </Router>
  );
}

export default App;
