import { useParams } from "react-router-dom";
import Detail from "../components/Detail.js";

export default function Details({ itemList }) {
  const { id } = useParams();

  return (
    <div>
      {itemList.map((item, key) => {
        return (
          <div key={key}>{item[1] === id ? <Detail item={item} /> : ""}</div>
        );
      })}
    </div>
  );
}
