import Display from "../components/Display";
import styles from "./Home.module.scss";
import { useState, useEffect } from "react";

function Home({ itemList }) {
  // waiting number
  const apiURL = "https://mvsale-berkeley.netlify.app/.netlify/functions/api";
  const [waits, setWaits] = useState([]);
  useEffect(() => {
    const getWaitList = async () => {
      try {
        const response = await fetch(`${apiURL}/wait_number`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({}),
        });
        // Check if the response is successful
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }
        // Parse the response JSON
        const result = await response.json();
        setWaits(result.wait);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    // Call the function
    getWaitList();
  }, []);

  return (
    <div className={styles.app}>
      <header className={styles.header}>
        <h1>Moving Sale</h1>
      </header>
      <section className={styles.keynote}>
        <h1>Notes</h1>
        <p> Location : North Berkeley (near the Safeway)</p>
        <p> Availability : Primarily from Nov. 21 to Nov. 23</p>
        <p> Please ensure you pick up the items by yourself</p>
        <p> Contact me anytime!</p>
        <p>&emsp;  (510)365-6772</p>
        <p>&emsp;  xsqesz@gmail.com</p>
      </section>
      <section className={styles.shopping}>
        {itemList.map((item, id) => {
          return (
            <div key={id}>
              {item[0] === "no" ? "" : <Display item={item} wait={waits[id]} />}
            </div>
          );
        })}
      </section>
    </div>
  );
}

export default Home;
