import styles from "./Display.module.scss";
import { Link } from "react-router-dom";
import { useEffect } from "react";

export default function Display({ item, wait }) {
  useEffect(() => {
    // When the component mounts, scroll to the saved position
    const savedScrollPosition = sessionStorage.getItem("scrollPosition");
    if (savedScrollPosition) {
      window.scrollTo(0, parseInt(savedScrollPosition));
    }

    // When the user scrolls, save the position in sessionStorage
    const saveScrollPosition = () => {
      sessionStorage.setItem("scrollPosition", window.scrollY);
    };

    window.addEventListener("scroll", saveScrollPosition);

    // Cleanup the event listener when the component unmounts
    return () => {
      window.removeEventListener("scroll", saveScrollPosition);
    };
  }, []);

  return (
    <div>
      <Link to={`item/${item[1]}`} className={styles.module}>
        <div className={styles.status}>
          <div className={wait !== 0 ? styles.reserved : styles.none}>
            <h1>Reserved</h1>
            <div>{wait}</div>
          </div>
        </div>
        <div className={styles.image}>
          <img src={require(`../styles/${item[1]}1.jpeg`)} alt={`${item[1]}`} />
        </div>
        <div className={styles.information}>
          <h2 className={styles.title}>{item[2]}</h2>
          <div className={styles.price}>
            <h2 className={styles.price_old}>{item[3]}</h2>
            <h2 className={styles.price_new}>{item[4]}</h2>
          </div>
        </div>
        <div className={styles.availability}>
          <h1 className={item[5] === "now" ? styles.avail_active : ""}>
            {item[5] === "now" ? "Available NOW!" : `Available from ${item[5]}`}
          </h1>
        </div>
      </Link>
    </div>
  );
}
